<template>
  <div class="content">
    <div class="container-fluid">
      <card>
        <h4 slot="header" class="card-title">건물 기울기</h4>
        <form>
          <div class="row">
            <div class="col-md-6">
              <base-input type="number"
                        label="건물 높이"
                        placeholder="단위 : mm"
                        v-model="pointA">
              </base-input>
            </div>
            <div class="col-md-6">
              <base-input type="number"
                        label="기울어진 거리"
                        placeholder="단위 : mm"
                        v-model="pointB">
              </base-input>
            </div>
          </div>
          <hr />
          <div class="clearfix"></div>
        </form>
      </card>

      <card v-if="hasgrade">
        <stats-card>
          <div slot="header" class="icon-warning">
            <i class="nc-icon nc-chart text-warning"></i>
          </div>
          <div slot="content">
            <p class="card-category">안전진단 등급</p>
            <h4 class="card-title">{{ this.grade }} <small>등급</small></h4>
          </div>
          <div slot="footer">
            <div class="table-responsive">
              <l-table v-if="hasgrade" class="table-hover"
                :columns="table.columns"
                :data="table.data">
            </l-table>
            </div>
          </div>
        </stats-card>
      </card>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/Cards/Card.vue';
  import LTable from 'src/components/Table.vue';

  export default {
    components: {
      Card,
      LTable
    },
    computed: {
      hasgrade () {
        return !!(this.pointA && this.pointB);
      },
      score () {
        return this.pointA / this.pointB;
      },
      grade () {
        if (!this.hasgrade) {
          return '-'
        }

        let grade = '-';
        if (750 <= this.score) {
          grade = 'A';
        } else if (500 <= this.score && this.score < 750) {
          grade = 'B'
        } else if (300 <= this.score && this.score < 500) {
          grade = 'C'
        } else if (200 <= this.score && this.score < 300) {
          grade = 'D'
        } else if (this.score < 200) {
          grade = 'E'
        }

        return grade;
      },
      table () {
        return {
          columns: ['점수'],
          data: [
            {
              '점수': this.score,
            }
          ]
        }
      }
    },
    data () {
      return {
        pointA: null,
        pointB: null,
        pointLength: 2,
        index: 0,
      }
    },
  }

</script>
