<template>
  <div class="content">
    <div class="container-fluid">
      <card>
        <h4 slot="header" class="card-title">부재처짐</h4>
        <form>
          <div class="row">
            <div class="col-md-12">
              <base-input type="number"
                        label="기둥과 기둥 사이 거리"
                        placeholder="단위 : mm"
                        v-model="distance">
              </base-input>
            </div>
          </div>

          <div class="row">
            <div class="col-md-4">
              <base-input type="number"
                        label="단부"
                        placeholder="단위 : mm"
                        v-model="pointA">
              </base-input>
            </div>
            <div class="col-md-4">
              <base-input type="number"
                        label="중앙부"
                        placeholder="단위 : mm"
                        v-model="pointB">
              </base-input>
            </div>
            <div class="col-md-4">
              <base-input type="number"
                        label="단부"
                        placeholder="단위 : mm"
                        v-model="pointC">
              </base-input>
            </div>
          </div>

          <div class="row">
            <base-checkbox v-model="isDefect">하자 여부 체크</base-checkbox>
          </div>
          
          <div class="clearfix"></div>
        </form>
      </card>

      <card v-if="hasgrade">
        <stats-card>
          <div slot="header" class="icon-warning">
            <i class="nc-icon nc-chart text-warning"></i>
          </div>
          <div slot="content">
            <p class="card-category">안전진단 등급</p>
            <h4 class="card-title">{{ this.grade }} <small>등급</small></h4>
          </div>
          <div slot="footer">
            <div class="table-responsive">
              <l-table v-if="hasgrade" class="table-hover"
                :columns="table.columns"
                :data="table.data">
            </l-table>
            </div>
          </div>
        </stats-card>
      </card>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/Cards/Card.vue';
  import LTable from 'src/components/Table.vue';

  export default {
    components: {
      Card,
      LTable
    },
    computed: {
      hasgrade () {
        return this.distance && this.pointA && this.pointB && this.pointC;
      },
      middlePoint () {
        return (Number(this.pointA) + Number(this.pointC)) / 2;
      },
      isPositiveNumber () {
        return 0 < (Number(this.pointA) + Number(this.pointC)) / 2;
      },
      deflection () {
        return this.middlePoint - Number(this.pointB);
      },
      score () {
        let deflectionPoint = this.isPositiveNumber ? this.deflection : -(this.deflection);
        return this.distance / deflectionPoint;
      },
      grade () {
        if (!this.hasgrade) {
          return '-'
        }

        let grade = '-';

        if (480 <= this.score) {
          grade = this.isDefect ? 'B' : 'A';
        } else if (240 <= this.score && this.score < 480) {
          grade = 'C'
        } else if (150 <= this.score && this.score < 240) {
          grade = 'D'
        } else if (this.score < 150) {
          grade = 'E'
        }

        return grade;
      },
      table () {
        return {
          columns: ['처짐량', '처짐환산치'],
          data: [
            {
              '처짐량': this.deflection,
              '처짐환산치': '1 / ' + this.score,
            }
          ]
        }
      }
    },
    data () {
      return {
        distance: null,
        pointA: null,
        pointB: null,
        pointC: null,
        isDefect: false,
      }
    },
    methods: {
      updateProfile () {
        alert('Your data: ' + JSON.stringify(this.user))
      }
    }
  }

</script>
