<template>
  <div class="content">
    <div class="container-fluid">
      <card>
        <h4 slot="header" class="card-title">부동침하</h4>
        <button class="btn btn-info btn-fill float-right" @click="onPointAdd">포인트 추가</button>
        <button class="btn btn-fill float-right delete-button" :class="{'disable': points.length <= 2}" @click="onPointDelete">포인트 삭제</button>
        <div class="clearfix"></div>
        <hr />
        <form v-for="(point, index) in points" :key="point.id">
          <h5 slot="header" class="title">포인트 {{ index + 1 }}</h5>
          <div class="row">
            <div class="col-md-12">
              <base-input type="number"
                        label="단부간 거리"
                        placeholder="단위 : mm"
                        v-model="point.distance">
              </base-input>
            </div>
          </div>

          <div class="row">
            <div class="col-md-6">
              <base-input type="number"
                        label="단부"
                        placeholder="단위 : mm"
                        v-model="point.pointA">
              </base-input>
            </div>
            <div class="col-md-6">
              <base-input type="number"
                        label="단부"
                        placeholder="단위 : mm"
                        v-model="point.pointB">
              </base-input>
            </div>
          </div>
          <hr />
          <div class="clearfix"></div>
        </form>
      </card>

      <card v-if="hasgrade">
        <stats-card>
          <div slot="header" class="icon-warning">
            <i class="nc-icon nc-chart text-warning"></i>
          </div>
          <div slot="content">
            <p class="card-category">안전진단 등급</p>
            <h4 class="card-title">{{ this.grade }} <small>등급</small></h4>
          </div>
          <div slot="footer">
            <div class="table-responsive">
              <l-table v-if="hasgrade" class="table-hover"
                :columns="table.columns"
                :data="table.data">
            </l-table>
            </div>
          </div>
        </stats-card>
      </card>
    </div>
  </div>
</template>
<script>
  import Card from 'src/components/Cards/Card.vue';
  import LTable from 'src/components/Table.vue';

  export default {
    components: {
      Card,
      LTable
    },
    computed: {
      hasgrade () {
        let hasGrade = null;
        this.points.forEach((point) => {
          if (hasGrade === false) return 
          hasGrade = !!(point.distance && point.pointA && point.pointB)
        });
        return hasGrade;
      },
      score () {
        let minScore = 0;
        this.points.forEach((point) => {
          let currentScore = point.distance / this.getDistance(point.pointA, point.pointB);
          if (minScore === 0) {
            minScore = currentScore;
          } else {
            minScore = (currentScore < minScore) ? currentScore : minScore;
          }
        });
        return minScore;
      },
      minScore () {
        let maxScore = 0;
        this.points.forEach((point) => {
          let currentScore = point.distance / this.getDistance(point.pointA, point.pointB);
          if (maxScore === 0) {
            maxScore = currentScore;
          } else {
            maxScore = (maxScore < currentScore) ? currentScore : maxScore;
          }
        });
        return maxScore;
      },
      grade () {
        if (!this.hasgrade) {
          return '-'
        }

        let grade = '-';

        if (750 <= this.score) {
          grade = 'A';
        } else if (500 <= this.score && this.score < 750) {
          grade = 'B'
        } else if (300 <= this.score && this.score < 500) {
          grade = 'C'
        } else if (200 <= this.score && this.score < 300) {
          grade = 'D'
        } else if (this.score < 200) {
          grade = 'E'
        }

        return grade;
      },
      table () {
        const columns = [];
        const data = {};
        const pointTitle = '포인트_';
        this.points.forEach((point, index) => {
          let title = `${pointTitle}${index + 1}`;
          columns.push(title)
          data[title] = point.distance / this.getDistance(point.pointA, point.pointB);
        })
        console.log(data)
        return {
          columns: columns,
          data: [data]
        }
      }
    },
    data () {
      return {
        points:[],
        pointLength: 2,
        index: 0,
      }
    },
    methods: {
      addPointLength () {
        this.pointLength++
      },
      getDefaultPoint () {
        return {
          distance: null,
          pointA: null,
          pointB: null,
          id: 'differentialSettlement_' + this.index,
        }
      },
      onPointAdd () {
        this.points.push(this.getDefaultPoint())
      },
      onPointDelete () {
        if (this.points.length <= 2) return;
        this.points.pop()
      },
      getDistance (pointA, pointB) {
        let distancePoint = pointA - pointB;
        return (distancePoint < 0) ? -(distancePoint) : distancePoint;
      }
    },
    mounted () {
      this.points.push(this.getDefaultPoint())
      this.index++
      this.points.push(this.getDefaultPoint())
    }
  }

</script>
<style>
.delete-button {
  margin-right: 5px;
}
</style>
